import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SelectBox from "../../SelectBox.jsx";
import TextField from "../../TextField";
import Button from "@material-ui/core/Button";
import Switch from "../../Switch";
import MultiLineTextField from "../../MultiLineTextField";
import RadioButton from "../../RadioButton";
import ContainedButtons from "../../Button";
import DatePicker from "../../DatePicker";
import TimePicker_dialog from "../../TimePicker_dialog";
import TimePicker_simple from "../../TimePicker_simple";


const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  list: {
    marginBottom: 5,
  }
}));

export default function SimpleExpansionPanel() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ExpansionPanel defaultExpanded>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            <h3 style={{ fontWeight: "normal", borderBottom: "solid 3px #4FC3F7" }}>
              
              </h3></Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography >
            {/* <h4 style={{fontWeight:"normal",borderBottom: "solid 1px #4FC3F7"}}>
              <Button disabled style={{color:"red"}}></Button>
            </h4> */}
            {/* <div style={{ display: "flex" }}> */}
            <TextField label="" />
            <TextField label="" />
            <TextField label="" />
            <br></br>
            <DatePicker label="" />
            {/* </div> */}
            <div style={{ display: "flex" }} >
              <Button style={{ margin: 10 }} disableElevation variant="contained" color="secondary">1</Button>
              <Button style={{ margin: 10 }} disableElevation variant="contained" color="secondary">2</Button>
              <Button style={{ margin: 10 }} disableElevation variant="contained" color="secondary">3</Button>
              <Button style={{ margin: 10 }} disableElevation variant="contained" color="secondary">4</Button>
              <Button style={{ margin: 10 }} disableElevation variant="contained" color="secondary">5</Button>
            </div>
            <br></br>
            <div style={{ display: "flex" }}>
              <TimePicker_dialog label="" />
              <TimePicker_simple label="" />

            </div>
            <div style={{ display: "flex" }}>
              <SelectBox value="" item1="12" item2="13" item3="14" />
              <SelectBox value="" item1="00" item2="15" item3="30" />

            </div>
            <br></br>
            <p style={{ fontSize: 12 }}></p>
            <div style={{ display: "flex" }}>
              <SelectBox value="" item1="" item2="" item3="" />
              <SelectBox value="" item1="" item2="" item3="" />

            </div>
            <TextField label="" />
            <h4 style={{ fontWeight: "normal", borderBottom: "solid 1px #4FC3F7" }}>
              
            </h4>
            <MultiLineTextField />
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>


    </div>
  );
}
