import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SelectBox from "../../SelectBox.jsx";
import TextField from "../../TextField";
import Button from "@material-ui/core/Button";
import Switch from "../../Switch";
import MultiLineTextField from "../../MultiLineTextField";
import RadioButton from "../RadioButton_Collabo";

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  list: {
    marginBottom: 5,
  }
}));

export default function SimpleExpansionPanel() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            <div style={{ display: "flex" }}>
              <h3 style={{ fontWeight: "normal", borderBottom: "solid 3px #4FC3F7" }}>
                
            </h3>
              <Button style={{ color: "#42A5F5" }}></Button>
            </div>
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography >
            <Button disableElevation variant="contained" color="secondary" style={{ marginBottom: 10 }}>
              </Button>
            <br></br>
            <div style={{ display: "flex" }}>
              <TextField label="" value="ABC" />
              <TextField label="" value="XYZ" />
            </div>
            <br></br>
            <ul>
              <li>
                <div style={{ display: "flex" }}>
                  <TextField label="" value="2020223" />
                  <TextField label="" value="" />
                </div>
              </li>
              <li>
                <div style={{ display: "flex" }}>
                  <TextField label="" value="2020226" />
                  <TextField label="" value="" />
                </div>
              </li>
            </ul>

            <br></br>
            <div style={{ display: "flex" }}>
              <div>
                <h5 style={{ fontWeight: "normal" }}>
                  
            </h5>
                <TextField label="1" value="" />
                <TextField label="2" value="JavaScript" />
                <TextField label="3" value="PHP" />
              </div>
              <div>
                <h5 style={{ fontWeight: "normal" }}>
                  
            </h5>
                <TextField label="" value="65" />
                <TextField label="" value="55" />
                <TextField label="" value="10" />
              </div>
            </div>
            <MultiLineTextField label="" value="" />
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel defaultExpanded>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            <div style={{ display: "flex" }}>
              <h3 style={{ fontWeight: "normal", borderBottom: "solid 3px #4FC3F7" }}>
                
              </h3>
              <Button disabled style={{color:"red"}}></Button>

            </div>
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography >
            <div style={{ display: "flex" }}>
              <TextField label="" value="2020223" />
              <SelectBox value="" item1="" item2="" item3="" />
              <TextField label="" />
            </div>
            <div style={{ display: "flex" }}>
              <TextField label="" value="2020226" />
              <SelectBox value="" item1="" item2="" item3="" />
              <TextField label="" />
            </div>
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>


    </div>
  );
}
