import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import SelectBox from "../../SelectBox.jsx";
import TextField from "../../TextField";
import Button from "@material-ui/core/Button";
import Switch from "../../Switch";
import MultiLineTextField from "../../MultiLineTextField";
import RadioButton from "../RadioButton_Collabo";

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  list: {
    marginBottom: 5,
  }
}));

export default function SimpleExpansionPanel() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ExpansionPanel>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            <div style={{ display: "flex" }}>
              <h3 style={{ fontWeight: "normal", borderBottom: "solid 3px #4FC3F7" }}>
                
            </h3>
              <Button style={{ color: "#42A5F5" }}></Button>
            </div>
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography >
            <div style={{ display: "flex" }}>
              <TextField label="" value="ABC" />
              <TextField label="" value="XYZ" />
            </div>
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>
      <ExpansionPanel defaultExpanded>
        <ExpansionPanelSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>
            <div style={{ display: "flex" }}>
              <h3 style={{ fontWeight: "normal", borderBottom: "solid 3px #4FC3F7" }}>
                
            </h3>
            </div>
          </Typography>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Typography >
            <span style={{display: "flex"}}>
              <Switch label="" /><Button disabled style={{color:"red"}}></Button>
            </span>
            <br></br>
            <h4 style={{ fontWeight: "normal", borderBottom: "solid 1px #4FC3F7" }}>
              
              <Button disabled style={{color:"red"}}></Button>
            </h4>
            <div style={{ display: "flex" }}>
              <SelectBox value="" item1="2020" />
              <SelectBox value="" item1="3" item2="4" item3="5" />
              <SelectBox value="" item1="1" item2="2" item3="3" />
            </div>
            <br></br>
            <TextField label="" />
            <h4 style={{ fontWeight: "normal", borderBottom: "solid 1px #4FC3F7" }}>
              
              <Button disabled style={{color:"red"}}></Button>
            </h4>
            <SelectBox value="1" item1="Javascript" item2="React" item3="Python" />
            <SelectBox value="2" item1="Javascript" item2="React" item3="Python" />
            <SelectBox value="3" item1="Javascript" item2="React" item3="Python" />
            <br></br>
              <h4 style={{ fontWeight: "normal", borderBottom: "solid 1px #4FC3F7" }}>
                
                <Button disabled style={{ color: "red" }}></Button>
              </h4>
            <div style={{ display: "flex" }}>
              <TextField label="" /><p></p>
            </div>
            <div style={{ display: "flex" }}>
              <TextField label="" /><p></p>
            </div>
            <div style={{ display: "flex" }}>
              <TextField label="" /><p></p>
            </div>
            <h4 style={{ fontWeight: "normal", borderBottom: "solid 1px #4FC3F7" }}>
              
            </h4>
            <MultiLineTextField />
          </Typography>
        </ExpansionPanelDetails>
      </ExpansionPanel>


    </div>
  );
}
